import React, { Component } from 'react';
import { NavBar, ImgCard, Intro, Papers, WorkExperience, TeachingExperience, OtherInterests } from './components';
import profile from './pkiourti.jpg';
import './App.css';

export default class Site extends Component {
  constructor(props) {
    super(props)

    this.goToIntro = this.goToIntro.bind(this)
    this.goToPapers = this.goToPapers.bind(this)
    this.goToExperience = this.goToExperience.bind(this)
    this.goToInterests = this.goToInterests.bind(this)
    this.state = {
      show_icon: false,
      show_intro: true,
      show_papers: false,
      show_experience: false,
      show_interests: false,
    }
  }

  goToIntro() {
    this.setState({ show_intro: true, show_papers: false, show_experience: false,  show_interests: false })
  }

  goToPapers() {
    this.setState({ show_papers: true, show_intro: false, show_experience: false,  show_interests: false })
  }

  goToExperience() {
    this.setState({ show_papers: false, show_intro: false, show_experience: true,  show_interests: false })
  }

  goToInterests() {
    this.setState({ show_papers: false, show_intro: false, show_experience: false, show_interests: true })
  }

  render() {
    return (
      <div>
        <NavBar 
          show_intro={this.state.show_intro}
          show_papers={this.state.show_papers}
          show_experience={this.state.show_experience}
          show_interests={this.state.show_interests}
          goToIntro={() => this.goToIntro()}
          goToPapers={() => this.goToPapers()}
          goToExperience={() => this.goToExperience()}
          goToInterests={() => this.goToInterests()}
        />
        <div className="card" style={{ 'display': this.state.show_intro ? 'block': 'none' }}>
          <div className="card-body"> 
            <div className="container">
              <div className="row">
                <div className="col-md-3">
                  <ImgCard 
                    profile={profile} 
                    github={this.state.show_icon}
                  />
                </div>
                <div className="col-md-9">
                  <Intro show_intro={this.state.show_intro}/>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="card" style={{ 'display': this.state.show_papers ? 'block': 'none' }}>
          <div className="card-body"> 
            <div className="container">
              <div className="row">
                <div className="col-md-3">
                </div>
                <div className="col-md-9">
                  <h1 className="heading">List of Papers</h1>
                  <Papers />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="card" style={{ 'display': this.state.show_experience ? 'block': 'none' }}>
          <div className="card-body"> 
            <div className="container">
              <div className="row">
                <div className="col-md-3">
                </div>
                <div className="col-md-9">
                  <h1 className="heading">Work Experience</h1>
                  <WorkExperience />
                  <h1 className="heading">Teaching Experience</h1>
                  <TeachingExperience />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="card" style={{ 'display': this.state.show_interests ? 'block': 'none' }}>
          <div className="card-body"> 
            <div className="container">
              <div className="row">
                <div className="col-md-3">
                </div>
                <div className="col-md-9">
                  <h1 className="heading">Other Interests</h1>
                  <OtherInterests />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
